
import XHeader from 'vux/src/components/x-header/index.vue'

import { types } from 'util';

export default {
name: 'FileLogin',
components: {
XHeader
},
created(){
var timer = setInterval(()=>{
this.countdown --;
if(this.countdown == 0){
clearInterval(timer);
}
},1000)
},
data () {
return {
PWD1:'',
PWD2:'',
type:'',
disable: true,
countdown: 10,
}

},
mounted(){
var router = this.$route.params;
this.type = router.type;


},
methods: {
NextStep(){
if(!this.disable && this.PWD1 == this.PWD2){
if(this.type=='1'){
this.NextStep1();
}else if(this.type=='2'){
this.NextStep2();
}
}else{
this.$vux.toast.text("两次输入密码不一致", "center");
}
},
NextStep1(){
this.$ajax.post('',{
'MSH.1':'healthFileAssociateService',
'MSH.2':'associate',
'token':this.$root.loginInfo.token,
'password':this.PWD1,
}).then(data => {
if(typeof data !='undefined'){
if(data.status==1){
this.$root.loginInfo.jkdabh = data.JKDABH;
this.$router.push('/FileLogin');
}else{
this.$vux.toast.text("操作失败", "bottom");
}
}
}).catch(error => {
console.log(error);
});

},
NextStep2(){
this.$ajax.post('',{
'MSH.1':'healthFileAssociateService',
'MSH.2':'forgotPassword',
'token':this.$root.loginInfo.token,
'mm':this.PWD1,
}).then(data => {
if(typeof data !='undefined'){
if(data.status==1){
this.$root.loginInfo.jkdabh = data.JKDABH;
console.log('档案编号2：'+this.$root.loginInfo.jkdabh)
this.$router.push('/FileLogin');
}else{
this.$vux.toast.text("操作失败", "bottom");
}
}
}).catch(error => {
console.log(error);
});

},
},

watch: {
PWD2(val){
if(val.length == 6){
this.disable = false;
return;
}
this.disable = true;
}
}
}



